import React, { Component } from 'react';
import axios from 'axios';

export default class FilesList extends Component {

    state = {
        files: []
    }

    componentDidMount(){
        this.getFiles();
    }

    async getFiles() {
        const res = await axios.get('https://backend-re-catalogo.herokuapp.com/api/files')
        this.setState({files: res.data})
    }

    deleteFile = async (id) => {
        await axios.delete('https://backend-re-catalogo.herokuapp.com/api/files/' + id)
        this.getFiles();
    }

    render() {
        return (
            <div className="files-list">
                <div className="files-list-container">
                {
                    this.state.files.map(file => (
                        <div  key={file._id} className="files-list-item">
                            <div>

                                <div className="files-list-item-texto">
                                    <h4>Identificador</h4>
                                    {
                                        file.identificador.map(identificador => (
                                            <p>{identificador}</p>
                                        ))
                                    }

                                    <h4>Imagem</h4>
                                    {
                                        file.imagem.map(imagem => (
                                            <img src={imagem} alt="" />
                                        ))
                                    }

                                    <h4>Ex-Prous</h4>
                                    {
                                        file.ex_prous.map(ex_prous => (
                                            <p>{ex_prous}</p>
                                        ))
                                    }

                                    <h4>Animal/Espécie</h4>
                                    {
                                        file.especie.map(especie => (
                                            <p>{especie}</p>
                                        ))
                                    }

                                    <h4>Paradeiro Atual</h4>
                                    {
                                        file.paradeiro_atual.map(paradeiro_atual => (
                                            <p>{paradeiro_atual}</p>
                                        ))
                                    }

                                    <h4>Paradeiros Anteriores</h4>
                                    {
                                        file.paradeiro_anterior.map(paradeiro_anterior => (
                                            <p>{paradeiro_anterior}</p>
                                        ))
                                    }

                                    <h4>Referências</h4>
                                    {
                                        file.referencia.map(referencia => (
                                            <p>{referencia}</p>
                                        ))
                                    }

                                    <h4>Região</h4>
                                    {
                                        file.região.map(região => (
                                            <p>{região}</p>
                                        ))
                                    }

                                    <h4>Sítio</h4>
                                    {
                                        file.sitio.map(sitio => (
                                            <p>{sitio}</p>
                                        ))
                                    }

                                    <h4>Contexto/Associação</h4>
                                    {
                                        file.contexto.map(contexto => (
                                            <p>{contexto}</p>
                                        ))
                                    }

                                    <h4>Integridade Anatômica</h4>
                                    {
                                        file.integridade.map(integridade => (
                                            <p>{integridade}</p>
                                        ))
                                    }

                                    <h4>Matéria Prima</h4>
                                    {
                                        file.materia_prima.map(materia_prima => (
                                            <p>{materia_prima}</p>
                                        ))
                                    }

                                    <h4>Dimensões/Densidade</h4>
                                    {
                                        file.dimensoes.map(dimensoes => (
                                            <p>{dimensoes}</p>
                                        ))
                                    }

                                    <h4>Aspectos Escultóricos</h4>
                                    {
                                        file.aspectos_escultorios.map(aspectos_escultorios => (
                                            <p>{aspectos_escultorios}</p>
                                        ))
                                    }

                                    <h4>Cavidade e Afins</h4>
                                    {
                                        file.cavidade.map(cavidade => (
                                            <p>{cavidade}</p>
                                        ))
                                    }

                                    <h4>Ocre</h4>
                                    {
                                        file.ocre.map(ocre => (
                                            <p>{ocre}</p>
                                        ))
                                    }

                                    <h4>Alteração</h4>
                                    {
                                        file.alteracao.map(alteracao => (
                                            <p>{alteracao}</p>
                                        ))
                                    }

                                    <h4>Tipologia Prousiana</h4>
                                    {
                                        file.tipologia_prousiana.map(tipologia_prousiana => (
                                            <p>{tipologia_prousiana}</p>
                                        ))
                                    }

                                    <h4>Observações</h4>
                                    {
                                        file.observacoes_bib.map(observacoes_bib => (
                                            <p>{observacoes_bib}</p>
                                        ))
                                    }

                                    <h4>Análise Ontológica</h4>
                                    {
                                        file.analise_ontologica.map(analise_ontologica => (
                                            <p>{analise_ontologica}</p>
                                        ))
                                    }

                                    <h4>Observações</h4>
                                    {
                                        file.observacoes_aut.map(observacoes_aut => (
                                            <p>{observacoes_aut}</p>
                                        ))
                                    }

                                    <h4>Galeria</h4>
                                    {
                                        file.galeria.map(galeria => (
                                            <img src={galeria} alt="" />
                                        ))
                                    }
                                    
                                    <h4>Colaboraram</h4>
                                    {
                                        file.colaboradores.map(colaboradores => (
                                            <p>{colaboradores}</p>
                                        ))
                                    }

                                    <h4>Categoria</h4>
                                    {
                                        file.categoria.map(categoria => (
                                            <p>{categoria}</p>
                                        ))
                                    }

                                </div>
                                <div className="delete-button-container">
                                    <button onClick={() => this.deleteFile(file._id)} className="delete-button">
                                        Delete
                                    </button>
                                </div>
                            </div>
                        </div>
                    ))
                }

                </div>
            </div>
        )
    }
}
