import React, { Component } from 'react';
import {Link} from 'react-router-dom';

export default class Navigation extends Component {
    render() {
        return (
            <nav >
                <div className="navigation-container">
                    <Link to="/">
                        (Re)Catálogo
                    </Link>

                    <div>
                        <ul className="navigation-container-buttons">
                            <li className ="navigation-container-buttons-button">
                                <Link to="/files">
                                    Catálogo
                                </Link>
                            </li>
                            <li className ="navigation-container-buttons-button">
                                <Link to="/create">
                                    preencher fichas
                                </Link>
                            </li>
                        </ul>
                    </div>
                </div>
            </nav>
        )
    }
}
