import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import './App.css';

import Navigation from './components/Navigation';
import Index from './components/index';
import FilesList from './components/FilesList';
import CreateFile from './components/CreateFile';

function App() {
  return (
    <div>

      <Router>
        <Navigation/>

        <div>
          <Route path="recatalogo-preenchimento/" exact component={Index}/>
          <Route path="recatalogo-preenchimento/files" component={FilesList}/>
          <Route path="recatalogo-preenchimento/edit/:id" component={CreateFile}/>
          <Route path="recatalogo-preenchimento/create" component={CreateFile}/>
        </div>

      </Router>

    </div>
  );
}

export default App;
