import React, { Component } from 'react';

export default class Navigation extends Component {
    render() {
        return (
            <nav className="index">
                <nav className="index-container">
                    <h1>Cadastro e verificação de zoólitos</h1>
                    <p>Esta aplicação tem como finalidade apenas o preenchimento e verificação das fichas de cadastro de zoólitos. 
                        As informações fornecidas aqui alimentarão o catalogo final do projeto.</p>
                    <hr/>
                    <h1>Importante</h1>
                    <p>A divisão entre paragrafos, imagens e itens da bibliografia deve ser feita por via do seguinte simbolo: (/)</p>
                    <br/>
                    <p>ex:</p>
                    <p>https://docs.google.com/uc?id=1YowqJ72zxCY_V926EK8zZReIofRpBuQl</p>
                    <p>(/)</p>
                    <p>https://docs.google.com/uc?id=1YowqJ72zxCY_V926EK8zZReIofRpBuQl</p>
                </nav>
            </nav>
        )
    }
}
