import React, { Component } from 'react';
import axios from 'axios';

export default class CreateFile extends Component {
    
    state={
        identificador: '',
        imagem: '',
        ex_prous: '',
        especie: '',
        paradeiro_atual: '',
        paradeiro_anterior: '',
        referencia: '',
        região: '',
        sitio: '',
        contexto: '',
        integridade: '',
        materia_prima: '',
        dimensoes: '',
        aspectos_escultorios: '',
        cavidade: '',
        ocre: '',
        alteracao: '',
        tipologia_prousiana: '',
        observacoes_bib: '',
        analise_ontologica: '',
        observacoes_aut: '',
        galeria: '',
        colaboradores: '',
        data_insercao: '',
        categoria: '',

        editing: false,
        _id: ''
    }

    async componentDidMount(){

        if (this.props.match.params.id) {
            const res = await axios.get('https://backend-re-catalogo.herokuapp.com/api/files/' + this.props.match.params.id);
            console.log(res.data)
            this.setState({
                identificador: res.data.identificador,
                imagem: res.data.imagem,
                ex_prous: res.data.ex_prous,
                especie: res.data.especie,
                paradeiro_atual: res.data.paradeiro_atual,
                paradeiro_anterior: res.data.paradeiro_anterior,
                referencia: res.data.referencia,
                região: res.data.região,
                sitio: res.data.sitio,
                contexto: res.data.contexto,
                integridade: res.data.integridade,
                materia_prima: res.data.materia_prima,
                dimensoes: res.data.dimensoes,
                aspectos_escultorios: res.data.aspectos_escultorios,
                cavidade: res.data.cavidade,
                ocre: res.data.ocre,
                alteracao: res.data.alteracao,
                tipologia_prousiana: res.data.tipologia_prousiana,
                observacoes_bib: res.data.observacoes_bib,
                analise_ontologica: res.data.analise_ontologica,
                observacoes_aut: res.data.observacoes_aut,
                galeria: res.data.galeria,
                colaboradores: res.data.colaboradores,
                categoria: res.data.categoria,
                data_insercao: res.data.data_insercao,

                editing: true,
                _id: this.props.match.params.id
            })
        }
    }

    onSubmit = async (e) => {
        e.preventDefault();

        const newFile = {
            identificador: this.state.identificador,
            imagem: this.state.imagem,
            ex_prous: this.state.ex_prous,
            especie: this.state.especie,
            paradeiro_atual: this.state.paradeiro_atual,
            paradeiro_anterior: this.state.paradeiro_anterior,
            referencia: this.state.referencia,
            região: this.state.região,
            sitio: this.state.sitio,
            contexto: this.state.contexto,
            integridade: this.state.integridade,
            materia_prima: this.state.materia_prima,
            dimensoes: this.state.dimensoes,
            aspectos_escultorios: this.state.aspectos_escultorios,
            cavidade: this.state.cavidade,
            ocre: this.state.ocre,
            alteracao: this.state.alteracao,
            tipologia_prousiana: this.state.tipologia_prousiana,
            observacoes_bib: this.state.observacoes_bib,
            analise_ontologica: this.state.analise_ontologica,
            observacoes_aut: this.state.observacoes_aut,
            galeria: this.state.galeria,
            colaboradores: this.state.colaboradores,
            categoria: this.state.categoria,
            data_insercao: this.state.data_insercao,
        };

        if(this.state.editing){
            await axios.put('https://backend-re-catalogo.herokuapp.com/api/files/' + this.state._id, newFile)
        } else {
            await axios.post('https://backend-re-catalogo.herokuapp.com/api/files', newFile);
        }
        //redireciona para a pagina de exibição das fichas
        window.location.href = 'recatalogo-preenchimento/files';
    }
    
    onInputChange = e =>{
        this.setState({
            [e.target.name]: e.target.value
        })
    }


    render() {
        return (
            <div className="create-file">
                <div className="create-file-container">
                    <h1>Preencher ficha</h1>

                    <div className="create-file-form">
                        <h4>Identificador</h4>
                        <textarea className="create-file-form-textarea"
                            name="identificador"
                            placeholder="identificador"
                            onChange={this.onInputChange}
                            value={this.state.identificador}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Imagem</h4>
                        <textarea className="create-file-form-textarea"
                            name="imagem"
                            placeholder="imagem"
                            onChange={this.onInputChange}
                            value={this.state.imagem}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Ex-Prous</h4>
                        <textarea className="create-file-form-textarea"
                            name="ex_prous"
                            placeholder="ex_prous"
                            onChange={this.onInputChange}
                            value={this.state.ex_prous}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Animal/Espécie</h4>
                        <textarea className="create-file-form-textarea"
                            name="especie"
                            placeholder="especie"
                            onChange={this.onInputChange}
                            value={this.state.especie}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Paradeiro Atual</h4>
                        <textarea className="create-file-form-textarea"
                            name="paradeiro_atual"
                            placeholder="paradeiro_atual"
                            onChange={this.onInputChange}
                            value={this.state.paradeiro_atual}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Paradeiros Anteriores</h4>
                        <textarea className="create-file-form-textarea"
                            name="paradeiro_anterior"
                            placeholder="paradeiro_anterior"
                            onChange={this.onInputChange}
                            value={this.state.paradeiro_anterior}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Referências</h4>
                        <textarea className="create-file-form-textarea"
                            name="referencia"
                            placeholder="referencia"
                            onChange={this.onInputChange}
                            value={this.state.referencia}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Região</h4>
                        <textarea className="create-file-form-textarea"
                            name="região"
                            placeholder="região"
                            onChange={this.onInputChange}
                            value={this.state.região}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Sítio</h4>
                        <textarea className="create-file-form-textarea"
                            name="sitio"
                            placeholder="sitio"
                            onChange={this.onInputChange}
                            value={this.state.sitio}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Contexto/Associação</h4>
                        <textarea className="create-file-form-textarea"
                            name="contexto"
                            placeholder="contexto"
                            onChange={this.onInputChange}
                            value={this.state.contexto}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Integridade Anatômica</h4>
                        <textarea className="create-file-form-textarea"
                            name="integridade"
                            placeholder="integridade"
                            onChange={this.onInputChange}
                            value={this.state.integridade}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Matéria Prima</h4>
                        <textarea className="create-file-form-textarea"
                            name="materia_prima"
                            placeholder="materia_prima"
                            onChange={this.onInputChange}
                            value={this.state.materia_prima}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Dimensões/Densidade</h4>
                        <textarea className="create-file-form-textarea"
                            name="dimensoes"
                            placeholder="dimensoes"
                            onChange={this.onInputChange}
                            value={this.state.dimensoes}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Aspectos Escultóricos</h4>
                        <textarea className="create-file-form-textarea"
                            name="aspectos_escultorios"
                            placeholder="aspectos_escultorios"
                            onChange={this.onInputChange}
                            value={this.state.aspectos_escultorios}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Cavidade e Afins</h4>
                        <textarea className="create-file-form-textarea"
                            name="cavidade"
                            placeholder="cavidade"
                            onChange={this.onInputChange}
                            value={this.state.cavidade}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Ocre</h4>
                        <textarea className="create-file-form-textarea"
                            name="ocre"
                            placeholder="ocre"
                            onChange={this.onInputChange}
                            value={this.state.ocre}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Alteração</h4>
                        <textarea className="create-file-form-textarea"
                            name="alteracao"
                            placeholder="alteracao"
                            onChange={this.onInputChange}
                            value={this.state.alteracao}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Tipologia Prousiana</h4>
                        <textarea className="create-file-form-textarea"
                            name="tipologia_prousiana"
                            placeholder="tipologia_prousiana"
                            onChange={this.onInputChange}
                            value={this.state.tipologia_prousiana}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Observações</h4>
                        <textarea className="create-file-form-textarea"
                            name="observacoes_bib"
                            placeholder="observacoes_bib"
                            onChange={this.onInputChange}
                            value={this.state.observacoes_bib}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Análise Ontológica</h4>
                        <textarea className="create-file-form-textarea"
                            name="analise_ontologica"
                            placeholder="analise_ontologica"
                            onChange={this.onInputChange}
                            value={this.state.analise_ontologica}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Observações</h4>
                        <textarea className="create-file-form-textarea"
                            name="observacoes_aut"
                            placeholder="observacoes_aut"
                            onChange={this.onInputChange}
                            value={this.state.observacoes_aut}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Galeria</h4>
                        <textarea className="create-file-form-textarea"
                            name="galeria"
                            placeholder="galeria"
                            onChange={this.onInputChange}
                            value={this.state.galeria}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Colaboraram</h4>
                        <textarea className="create-file-form-textarea"
                            name="colaboradores"
                            placeholder="colaboradores"
                            onChange={this.onInputChange}
                            value={this.state.colaboradores}
                            required
                        >
                        </textarea>
                    </div>

                    <div className="create-file-form">
                        <h4>Categoria</h4>
                        <textarea className="create-file-form-textarea"
                            name="categoria"
                            placeholder="categoria"
                            onChange={this.onInputChange}
                            value={this.state.categoria}
                            required
                        >
                        </textarea>
                    </div>
                    
                    <form onSubmit={this.onSubmit}>
                        <button className="create-file-button" type="submit">
                            save
                        </button>
                    </form>
                </div>
            </div>
        )
    }
}
